import React, { Fragment } from 'react'
import { Row, Col, FormGroup } from 'reactstrap'
import { Rating } from '@material-ui/lab'
import FavoriteIcon from '@material-ui/icons/Favorite'
import CardDescProduct from '../Components/CardDescProduct'
import CardOrderComponent from '../Components/CardOrderComponent'
import CardRatingProduct from '../Components/CardRatingComponent'
import CardReviewComponent from '../Components/CardReviewComponent'

function ProductSection(props) {

    return(
        <Fragment>
            <Row>
               <Col md="12">
                    <FormGroup>
                        <h1>Ini Product {props.match.params.id} </h1>
                        <Rating size="small" style={{color: '#ff3d47'}} defaultValue={4} precision={1} icon={<FavoriteIcon fontSize="small"/>} readOnly/>
                    </FormGroup>
               </Col>
               <Col lg="6" md="12" className="mb-3">
                    <img src="data:image/jpeg;base64," className="img-thumbnail shadow" style={{maxHeight: '360px', width: '100%'}} alt="product" />
               </Col>
               <Col lg="6" md="12" className="mb-3">
                    <CardOrderComponent price="100000" />
               </Col>
               <Col lg="6" md="12" className="mb-3">
                    <CardDescProduct />
               </Col>
               <Col lg="6" md="12" className="mb-3">
                    <CardRatingProduct />
               </Col>
               <Col md="12">
                    <CardReviewComponent />
               </Col>
            </Row>
        </Fragment>
    )
}

export default ProductSection