import React, { Fragment } from 'react'
import { Card, CardBody, CardHeader, Badge } from 'reactstrap'
import Slider from 'react-slick'
import { Link } from 'react-router-dom'

function CardBSComponent() {

    const settings = {
        arrows: false,
        dots: true,
        infinite: true,
        slidesToShow: 2,
        speed: 500,
        slidesToScroll: 1,
        autoplay: true,
        focusOnSelect: false,
        customPaging: i => (
            <Badge className="pill-slide" pill>&nbsp;</Badge>
        )
    }

    return(
        <Fragment>
            <Card className="shadow card-default">
                <CardHeader>
                    <h3>Terlaris</h3>
                </CardHeader>
                <CardBody>
                        <Slider {...settings} className="mb-3">
                            <div>
                                <Link to="/product/1">
                                    <img src="data:image/jpeg;base64," id={1} className="img-thumbnail res-slider" price="100.000" title="Bolu" alt="product" />
                                </Link>
                            </div>
                            <div>
                                <Link to="/product/2">
                                    <img src="data:image/jpeg;base64," id={1} className="img-thumbnail res-slider" price="100.000" title="Bolu" alt="product" />
                                </Link>
                            </div>
                            <div>
                                <Link to="/product/1">
                                    <img src="data:image/jpeg;base64," id={1} className="img-thumbnail res-slider" price="100.000" title="Bolu" alt="product" />
                                </Link>
                            </div>
                            <div>
                                <Link to="/product/2">
                                    <img src="data:image/jpeg;base64," id={1} className="img-thumbnail res-slider" price="100.000" title="Bolu" alt="product" />
                                </Link>
                            </div>
                        </Slider>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default CardBSComponent