import React, { Fragment, lazy, Suspense } from 'react'
import { Row, Col, Button } from 'reactstrap'
import { SkeletonCard, SkeletonBanner, SkeletonBS, SkeletonSearch } from '../Components/SkeletonComponent'
import BannerComponent from '../Components/BannerComponent'
import CardBSComponent from '../Components/CardBSComponent'
import FilterComponent from '../Components/FilterComponent'

const CardItemComponent = lazy(() => import('../Components/CardItemComponent'))

function HomeSection() {

    return(
        <Fragment>
            <Row>
                <Col lg="12" md="12" className="col-12 mb-5">
                    <Suspense fallback={<SkeletonBanner />} >
                        <BannerComponent />
                    </Suspense>
                </Col>
                <Col lg="6" md="6" className="col-12 mb-5">
                    <Suspense fallback={<SkeletonBS />}>
                        <CardBSComponent />
                    </Suspense>
                </Col>
                <Col lg="6" md="6" className="col-12 mb-5">
                    <Suspense fallback={<SkeletonBS />}>
                        <CardBSComponent />
                    </Suspense>
                </Col>
                <Col lg="12" md="12" className="col-12 mb-5">
                    <Suspense fallback={<SkeletonSearch />} >
                        <FilterComponent />
                    </Suspense>
                </Col>
                <Col lg="4" md="6" className="col-12 mb-3">
                    <Suspense fallback={<SkeletonCard />}>
                        <CardItemComponent thumbnail="data:image/jpeg;base64," id={1} price="100000" title="Bolu" />
                    </Suspense>
                </Col>
                <Col lg="4" md="6" className="col-12 mb-3">
                    <Suspense fallback={<SkeletonCard />}>
                        <CardItemComponent thumbnail="data:image/jpeg;base64," id={2} price="100000" title="Bolu" />
                    </Suspense>
                </Col>
                <Col lg="4" md="6" className="col-12 mb-3">
                    <Suspense fallback={<SkeletonCard />}>
                        <CardItemComponent thumbnail="data:image/jpeg;base64," id={1} price="100000" title="Bolu" />
                    </Suspense>
                </Col>
                <Col lg="12" md="12" className="col-12 mb-3">
                    <Suspense fallback={<SkeletonCard />}>
                        <Button size="lg" className="btn-brown-outline" outline block>Lihat Lainnya</Button>
                    </Suspense>
                </Col>
            </Row>
        </Fragment>
    )
}

export default HomeSection